.input-field {
    width: 100%;
    position: relative;
    box-sizing: border-box;
    margin-top: 30px;
}

.input-field>i {
    background-image: url("../../assets/search-solid.svg");
    height: 30px;
    width: 30px;
    background-size: 30px 30px;
    background-repeat: no-repeat;
    position: absolute;
    right: 30px;
    top: 5px;
    opacity: .5;
    z-index: 5;
    cursor: pointer;
    transition: all .5s;
}

.input-field>i:hover {
    opacity: 1;
}

